.svg {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -250px;
  margin-left: -300px;
}
.messageBox {
  height: 200px;
  width: 380px;
  position: absolute;
  top: 50%;
  left: 60%;
  margin-top: -100px;
  margin-left: 50px;
  color: #fff;
  font-family: Roboto;
  font-weight: 300;
}
.messageBox h1 {
  font-size: 60px;
  line-height: 46px;
  margin-bottom: 40px;
  color: #5a5c6c;
}
.messageBox p {
  font-size: 20px;
  color: #5a5c6c;
}
.buttonsCon .actionLinkWrap {
  margin-top: 40px;
}
.buttonsCon .actionLinkWrap a {
  background: #68c950;
  padding: 8px 25px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  font-size: 14px;
  transition: all 0.3s linear;
  cursor: pointer;
  text-decoration: none;
  margin-right: 10px;
}
.buttonsCon .actionLinkWrap a:hover {
  background: #5a5c6c;
  color: #fff;
}

#Polygon1,
#Polygon2,
#Polygon3,
#Polygon4,
#Polygon4,
#Polygon5 {
  animation: float 1s infinite ease-in-out alternate;
}
#Polygon2 {
  animation-delay: 0.2s;
}
#Polygon3 {
  animation-delay: 0.4s;
}
#Polygon4 {
  animation-delay: 0.6s;
}
#Polygon5 {
  animation-delay: 0.8s;
}

@keyframes float {
  100% {
    transform: translateY(20px);
  }
}
@media (max-width: 766px) {
  .svg {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -250px;
    margin-left: -190px;
  }
  .messageBox {
    top: 50%;
    left: 50%;
    margin-top: -100px;
    margin-left: -190px;
    text-align: center;
  }
}
